import { isArray, isBoolean, isNull, isNumber, isObject, isString, isUndefined } from '../utils/is';
import {
  ArrayValueTypeDescriptor,
  ObjectValueTypeDescriptor,
  ObjectValueTypeDescriptorProperties,
  PrimitiveValueTypeDescriptor,
  ValueType,
  ValueTypeDescriptor,
} from './extracted-value-types';

export interface ObjectValue {
  [property: string]: unknown;
}

export class TypeExtractor {
  private static throwInvalidSourceError(source: unknown) {
    throw new TypeError(
      '[remote-config] Could not extract the types: Expected to receive an object, ' + `but got ${typeof source}`,
    );
  }

  private static extractObjectType(value: ObjectValue): ObjectValueTypeDescriptor {
    const properties: ObjectValueTypeDescriptorProperties = {};

    for (const propertyName of Object.keys(value)) {
      const propertyValue = value[propertyName];
      const propertyValueType = TypeExtractor.extractType(propertyValue);

      if (propertyValueType) {
        properties[propertyName] = propertyValueType;
      }
    }

    return new ObjectValueTypeDescriptor(properties);
  }

  private static extractType(value: unknown): ValueTypeDescriptor | null {
    if (isUndefined(value)) {
      return null;
    }

    if (isArray(value)) {
      return new ArrayValueTypeDescriptor();
    }

    if (isBoolean(value)) {
      return new PrimitiveValueTypeDescriptor(ValueType.Boolean);
    }

    if (isNull(value)) {
      return new PrimitiveValueTypeDescriptor(ValueType.Null);
    }

    if (isNumber(value)) {
      return new PrimitiveValueTypeDescriptor(ValueType.Number);
    }

    if (isString(value)) {
      return new PrimitiveValueTypeDescriptor(ValueType.String);
    }

    return TypeExtractor.extractObjectType(value as ObjectValue);
  }

  extractTypes(source: ObjectValue): ObjectValueTypeDescriptor {
    if (!isObject(source)) {
      TypeExtractor.throwInvalidSourceError(source);
    }

    return TypeExtractor.extractObjectType(source);
  }
}
