import { RemoteConfigParameters } from '../remote-config.types';

export interface RemoteConfigRequestParameters {
  headers: {
    'Application-Name': string;
    'Application-Version': string;
    'Custom-Tags': string;
  };
  url: string;
}

export abstract class ConfigFetcher<T extends RemoteConfigParameters> {
  abstract fetch(requestParameters: RemoteConfigRequestParameters): Promise<Partial<T>>;
}
