import { ConfigFetcher } from './config-fetchers/abstract';
import { ParseFailure, ParseResult, ParseSuccess } from './parser/parse-result';
import { ParseError, ValueParser } from './parser/value-parser';
import { RemoteConfigContext, RemoteConfigParameters } from './remote-config.types';
import { ObjectValueTypeDescriptor } from './type-extractor/extracted-value-types';
import { ObjectValue, TypeExtractor } from './type-extractor/type-extractor';
import { hasProperty } from './utils/has-property';
import { isNullOrUndefined, isUndefined } from './utils/is';
import { trim } from './utils/trim';

const valueParser = new ValueParser();
const typeExtractor = new TypeExtractor();

export class RemoteConfig<T extends RemoteConfigParameters> {
  constructor(private readonly configFetcher: ConfigFetcher<T>, context: RemoteConfigContext, defaults: T) {
    const normalizedContext = RemoteConfig.normalizeContext(context);

    RemoteConfig.validateContext(normalizedContext);

    this.context = normalizedContext;

    this.defaultConfig = {
      ...defaults,
    };

    this.extractedType = typeExtractor.extractTypes(defaults as ObjectValue);
  }

  private static normalizeTag(tag: string) {
    return trim(tag);
  }

  private static normalizeContext(context: RemoteConfigContext): RemoteConfigContext {
    const appName = trim(context.appName);
    const appVersion = trim(context.appVersion);
    const projectName = trim(context.projectName);
    const serviceRoot = trim(context.serviceRoot).replace(/\/$/, '');

    return {
      appName,
      appVersion,
      projectName,
      serviceRoot,
    };
  }

  private static validateContext(context: RemoteConfigContext): void {
    if (context.appName.length === 0) {
      throw new TypeError(
        `[remote-config] Could not instantiate Remove Config client: ` + `The provided appName is empty.`,
      );
    }

    if (context.appVersion.length === 0) {
      throw new TypeError(
        `[remote-config] Could not instantiate Remove Config client: ` + `The provided appVersion is empty.`,
      );
    }
  }
  private activatedConfig?: Partial<T>;

  private fetchedConfig?: Partial<T>;

  private readonly context: RemoteConfigContext;

  private readonly defaultConfig: T;

  private readonly extractedType: ObjectValueTypeDescriptor;

  private readonly tags = new Set<string>();

  activate(): void {
    this.activatedConfig = Object.assign({}, this.fetchedConfig);
  }

  addCustomTag(tag: string): void {
    const normalizedTag = RemoteConfig.normalizeTag(tag);

    if (normalizedTag.length === 0) {
      console.warn(`[remote-config] Could not add the tag: ` + `A tag must be a non-empty string.`);

      return;
    }

    this.tags.add(normalizedTag);
  }

  removeCustomTag(tag: string): boolean {
    const normalizedTag = RemoteConfig.normalizeTag(tag);

    if (normalizedTag.length === 0) {
      console.warn(`[remote-config] Could not delete the tag: ` + `A tag must be a non-empty string.`);

      return false;
    }

    return this.tags.delete(normalizedTag);
  }

  clearCustomTags() {
    this.tags.clear();
  }

  async fetch(): Promise<void> {
    const projectName = this.context.projectName;
    const serviceRoot = this.context.serviceRoot;

    const url = `${serviceRoot}/client-api/v1/projects/${projectName}/parameters`;

    const headers = {
      'Application-Name': this.context.appName,
      'Application-Version': this.context.appVersion,
      'Custom-Tags': Array.from(this.tags).join(','),
    };

    try {
      this.fetchedConfig = await this.configFetcher.fetch({
        headers,
        url,
      });
    } catch (error) {
      console.warn(`[remote-config] Could not fetch the remote config:`, error);
    }
  }

  async fetchAndActivate(): Promise<void> {
    await this.fetch();
    this.activate();
  }

  getValue<K extends keyof T & string>(parameterName: K): T[K] | null {
    const remoteValue = this.getRemoteValue(parameterName);

    if (remoteValue instanceof ParseSuccess) {
      return remoteValue.result;
    }

    if (remoteValue instanceof ParseFailure) {
      console.warn(
        `[remote-config] Failed to parse the remote value ` +
          `of the parameter "${parameterName}". ` +
          `Reason: ${remoteValue.reason}`,
      );
    }

    return this.getDefaultValue(parameterName);
  }

  private getDefaultValue<K extends keyof T & string>(parameterName: K): T[K] | null {
    if (hasProperty(this.defaultConfig, parameterName)) {
      const defaultValue = this.defaultConfig[parameterName];

      return isNullOrUndefined(defaultValue) ? null : defaultValue;
    }

    console.warn(
      `[remote-config] Could not get a default value. ` +
        `The parameter '${parameterName}' is not present in the default config.`,
    );

    return null;
  }

  private getRawRemoteValue<K extends keyof T & string>(parameterName: K): unknown | undefined {
    if (this.activatedConfig && hasProperty(this.activatedConfig, parameterName)) {
      return this.activatedConfig[parameterName];
    }

    return undefined;
  }

  private getRemoteValue<K extends keyof T & string>(parameterName: K): ParseResult<T[K], ParseError> | null {
    const rawRemoteValue = this.getRawRemoteValue(parameterName);

    return !isUndefined(rawRemoteValue) ? this.parseRemoteValue(rawRemoteValue, parameterName) : null;
  }

  private parseRemoteValue<K extends keyof T & string>(
    rawRemoteValue: unknown,
    parameterName: K,
  ): ParseResult<T[K], ParseError> {
    const valueType = this.extractedType.properties[parameterName];

    if (!valueType) {
      return ParseFailure.create(ParseError.UnknownPropertyType);
    }

    return valueParser.parseAs<T[K]>(rawRemoteValue, valueType);
  }
}
