export class ParseSuccess<T> {
  static create<T>(result: T) {
    return new ParseSuccess(result);
  }

  constructor(readonly result: T) {}
}

export class ParseFailure<R> {
  static create<R>(reason: R) {
    return new ParseFailure(reason);
  }

  constructor(readonly reason: R) {}
}

export type ParseResult<T, R> = ParseSuccess<T> | ParseFailure<R>;
