import { Inject, Injectable, InjectionToken } from '@angular/core';
import { RemoteConfig } from '../core/remote-config';
import { RemoteConfigParameters } from '../core/remote-config.types';
import { RemoteConfigFetcherService } from './remote-config-fetcher.service';
import { RemoteConfigConfiguration } from './remote-config.types';

export const RemoteConfigConfigurationToken = new InjectionToken<RemoteConfigConfiguration<any>>(
  '@backbase/remote-config-ang: RemoteConfigConfigurationToken',
);

@Injectable()
export class RemoteConfigService<T extends RemoteConfigParameters> {
  private readonly remoteConfigClient: RemoteConfig<T>;

  private readonly disabled: boolean;

  constructor(
    @Inject(RemoteConfigConfigurationToken) private readonly configuration: RemoteConfigConfiguration<T>,
    private readonly configFetcher: RemoteConfigFetcherService<T>,
  ) {
    this.remoteConfigClient = new RemoteConfig<T>(
      this.configFetcher,
      {
        appName: configuration.appName,
        appVersion: configuration.appVersion,
        projectName: configuration.projectName,
        serviceRoot: configuration.serviceRoot,
      },
      configuration.defaults,
    );

    this.disabled = Boolean(configuration.disabled);
  }

  addCustomTag(tag: string): void {
    this.remoteConfigClient.addCustomTag(tag);
  }

  clearCustomTags(): void {
    this.remoteConfigClient.clearCustomTags();
  }

  getValue<K extends keyof T & string>(parameterName: K): T[K] {
    return this.remoteConfigClient.getValue(parameterName) as T[K];
  }

  fetchAndActivate(): Promise<void> {
    if (this.disabled) {
      return Promise.resolve();
    }

    return this.remoteConfigClient.fetchAndActivate();
  }

  removeCustomTag(tag: string): boolean {
    return this.remoteConfigClient.removeCustomTag(tag);
  }
}
