import { ModuleWithProviders, NgModule } from '@angular/core';
import { RemoteConfigFetcherService } from './remote-config-fetcher.service';
import { RemoteConfigConfigurationToken, RemoteConfigService } from './remote-config.service';
import { RemoteConfigConfiguration } from './remote-config.types';

@NgModule({
  imports: [],
  declarations: [],
  providers: [RemoteConfigFetcherService, RemoteConfigService],
  exports: [],
})
export class RemoteConfigModule {
  static forRoot<T>(configuration: RemoteConfigConfiguration<T>): ModuleWithProviders<RemoteConfigModule> {
    return {
      ngModule: RemoteConfigModule,
      providers: [
        {
          provide: RemoteConfigConfigurationToken,
          useValue: configuration,
        },
      ],
    };
  }
}
