export const isArray = (arg: unknown): boolean => Array.isArray(arg);

export const isBoolean = (arg: unknown): boolean => typeof arg === 'boolean';

export const isNull = (arg: unknown): boolean => arg === null;

export const isNullOrUndefined = (arg: unknown): boolean => isNull(arg) || isUndefined(arg);

export const isNumber = (arg: unknown): boolean => typeof arg === 'number';

export const isString = (arg: unknown): boolean => typeof arg === 'string';

export const isObject = (arg: unknown): boolean =>
  typeof arg === 'object' && arg !== null && Array.isArray(arg) === false;

export const isUndefined = (arg: unknown): boolean => typeof arg === 'undefined';
