import { ConfigFetcher } from '../core/config-fetchers/abstract';
import { RemoteConfigParameters, RemoteConfigRequestParameters } from '../core/remote-config.types';

import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class RemoteConfigFetcherService<T extends RemoteConfigParameters> implements ConfigFetcher<T> {
  constructor(private readonly http: HttpClient) {}

  fetch(requestParameters: RemoteConfigRequestParameters): Promise<T> {
    return this.http
      .get<T>(requestParameters.url, {
        headers: requestParameters.headers,
      })
      .toPromise();
  }
}
