import {
  ArrayValueTypeDescriptor,
  ObjectValueTypeDescriptor,
  PrimitiveValueTypeDescriptor,
  ValueType,
  ValueTypeDescriptor,
} from '../type-extractor/extracted-value-types';
import { isArray, isBoolean, isNull, isNumber, isObject, isString } from '../utils/is';
import { ParseFailure, ParseResult, ParseSuccess } from './parse-result';

interface UnknownObject {
  [property: string]: unknown;
}

export enum ParseError {
  TypeMismatch = 'TypeMismatch',
  UnknownPropertyType = 'UnknownPropertyType',
}

export class ValueParser {
  parseAs<T>(rawValue: unknown, valueType: ValueTypeDescriptor): ParseResult<T, ParseError> {
    if (valueType instanceof ArrayValueTypeDescriptor) {
      return this.parseArray<T>(rawValue);
    }

    if (valueType instanceof ObjectValueTypeDescriptor) {
      return this.parseObjectAs<T>(rawValue, valueType);
    }

    return this.parsePrimitiveAs<T>(rawValue, valueType);
  }

  private parseArray<T>(value: unknown): ParseResult<T, ParseError> {
    return isArray(value) ? ParseSuccess.create(value as T) : ParseFailure.create(ParseError.TypeMismatch);
  }

  private parseObjectAs<T>(rawValue: unknown, valueType: ObjectValueTypeDescriptor): ParseResult<T, ParseError> {
    if (!isObject(rawValue)) {
      return ParseFailure.create(ParseError.TypeMismatch);
    }

    const objectRawValue = rawValue as UnknownObject;
    const result = {} as UnknownObject;

    for (const propertyName of Object.keys(valueType.properties)) {
      const propertyValueType = valueType.properties[propertyName];
      const propertyValueParseResult = this.parseAs<T>(objectRawValue[propertyName], propertyValueType);

      if (propertyValueParseResult instanceof ParseFailure) {
        return ParseFailure.create(propertyValueParseResult.reason);
      }

      result[propertyName] = propertyValueParseResult.result;
    }

    return ParseSuccess.create(result as unknown as T);
  }

  private parsePrimitiveAs<T>(value: unknown, valueType: PrimitiveValueTypeDescriptor): ParseResult<T, ParseError> {
    if (valueType.type === ValueType.Boolean) {
      return isBoolean(value) ? ParseSuccess.create(value as T) : ParseFailure.create(ParseError.TypeMismatch);
    }

    if (valueType.type === ValueType.Number) {
      return isNumber(value) ? ParseSuccess.create(value as T) : ParseFailure.create(ParseError.TypeMismatch);
    }

    if (valueType.type === ValueType.Null) {
      return isNull(value) ? ParseSuccess.create(value as T) : ParseFailure.create(ParseError.TypeMismatch);
    }

    return isString(value) ? ParseSuccess.create(value as T) : ParseFailure.create(ParseError.TypeMismatch);
  }
}
