export enum ValueType {
  Array = 'array',
  Boolean = 'boolean',
  Object = 'object',
  Null = 'null',
  Number = 'number',
  String = 'string',
}

export type PrimitiveValueType = ValueType.Boolean | ValueType.Null | ValueType.Number | ValueType.String;

export class ArrayValueTypeDescriptor {
  readonly type = ValueType.Array;
}

export class ObjectValueTypeDescriptor {
  readonly type = ValueType.Object;

  constructor(readonly properties: ObjectValueTypeDescriptorProperties) {}
}

export class PrimitiveValueTypeDescriptor {
  constructor(readonly type: PrimitiveValueType) {}
}

export type ValueTypeDescriptor = ArrayValueTypeDescriptor | ObjectValueTypeDescriptor | PrimitiveValueTypeDescriptor;

export interface ObjectValueTypeDescriptorProperties {
  [propertyName: string]: ValueTypeDescriptor;
}
